import React, { useState, useEffect } from "react";
import axios from "axios";
import "./App.css";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const App = () => {
  const [modal, setModal] = useState(null);
  useEffect(() => {
    loadData();
  }, []);
  const loadData = async () => {
    const request = { mobile: localStorage.getItem("mobile") };
    const data = JSON.parse(localStorage.getItem("Details"));
    if (data !== null) {
      setModal(data);
    } else if (request) {
      const res = await axios.post("/details", request);
      if (res.data.length !== 0) {
        setModal(res.data[0]);
      }
    }
  };

  const [details, setDetails] = useState({
    fname: "",
    lname: "",
    mobile: -999,
    address: "",
    city: "",
    pincode: "",
    gender: "",
    storage: "",
  });

  const hide = () => {
    document.getElementById("modal-box").style.display = "none";
  };

  const trigger = (e) => {
    const value = e.target.value;
    setDetails({
      ...details,
      [e.target.name]: value,
    });
  };

  const save = async () => {
    if (details.storage === "Local") {
      localStorage.setItem("Details", JSON.stringify(details));
      toast.success("Data Saved to Local Storage!", {
        position: "top-center",
      });
    } else {
      const response = await axios.post("/save", details);
      if (response) {
        localStorage.setItem("mobile", response.data.mobile);
        toast.success(response.data.status, { position: "top-center" });
      }
    }
  };

  return (
    <>
      {(() => {
        if (modal != null) {
          return (
            <div id="modal-box">
              <div id="upper-modal">
                <div id="modal-heading">
                  <h2>Welcome {`${modal.fname} ${modal.lname}`}</h2>
                </div>
                <div onClick={hide} id="modal-close">
                  x
                </div>
              </div>
              <div id="lower-modal">
                <p>{`Name: ${modal.fname} ${modal.lname}`}</p>
                <br />
                <p>{`Gender: ${modal.gender}`}</p>
                <br />
                <p>{`Mobile: ${modal.mobile}`}</p>
                <br />
                <p>{`Address: ${modal.address}`}</p>
                <br />
                <p>{`City: ${modal.city}`}</p>
                <br />
                <p>{`Pincode: ${modal.pincode}`}</p>
                <div className="close">
                  <button id="close-btn" onClick={hide}>
                    Close
                  </button>
                </div>
              </div>
            </div>
          );
        }
      })()}
      <div className="design mb-3 fonts">
        <h2>Store Contact Information</h2>
      </div>
      <div className="row justify-content-center mb-2 mt-1">
        <div className="col-6">
          <div className="form-group mb-2">
            <input
              type="text"
              name="fname"
              className="form-control"
              placeholder="Enter First Name"
              onChange={trigger}
            />
          </div>
          <div className="form-group mb-2">
            <input
              type="text"
              name="lname"
              className="form-control"
              placeholder="Enter Last Name"
              onChange={trigger}
            />
          </div>
          <div className="form-group mb-2">
            <input
              type="number"
              name="mobile"
              className="form-control"
              placeholder="Enter Mobile No"
              onChange={trigger}
            />
          </div>
          <div className="form-group mb-2">
            <input
              type="text"
              name="address"
              className="form-control"
              placeholder="Enter Your Address"
              onChange={trigger}
            />
          </div>
          <div className="form-group mb-2">
            <input
              type="text"
              name="city"
              className="form-control"
              placeholder="Enter Your City"
              onChange={trigger}
            />
          </div>
          <div className="form-group mb-2">
            <input
              type="number"
              name="pincode"
              className="form-control"
              placeholder="Enter Pincode"
              onChange={trigger}
            />
          </div>
          <div className="form-group mb-2">
            <div className="form-check form-check-inline">
              <input
                className="form-check-input"
                type="radio"
                name="gender"
                value="male"
                onChange={trigger}
              />
              <label className="form-check-label" for="inlineRadio1">
                Male
              </label>
            </div>
            <div className="form-check form-check-inline">
              <input
                className="form-check-input"
                type="radio"
                name="gender"
                value="female"
                onChange={trigger}
              />
              <label className="form-check-label" for="inlineRadio2">
                Female
              </label>
            </div>
          </div>
          <div className="form-group mb-2">
            <label>Save To*</label>
            <select
              type="text"
              name="storage"
              className="form-control"
              placeholder="Enter Storage Type"
              onChange={trigger}
            >
              <option>Select</option>
              <option value="Local">Local</option>
              <option value="Database">Database</option>
            </select>
          </div>
        </div>
      </div>
      <div className="mb-2 design">
        <button onClick={save} className="btn btn-success">
          Save
        </button>
      </div>
      <ToastContainer />
    </>
  );
};

export default App;
